
// Works slider

$(".works-slider").slick({
    arrows: false,
    dots: true,
    slidesToShow: 3,
    slidesToScroll: 3,
    speed: 700,
    infinite: false,
    responsive: [
        {
            breakpoint: 1039.98,
            settings: {
                slidesToScroll: 2,
                slidesToShow: 2,
            }
        },
        {
            breakpoint: 767.98,
            settings: {
                slidesToScroll: 1,
                slidesToShow: 1,
            }
        }
    ]
})

// Poll
$(".poll__item").on("click", function() {
    $(".poll__item").removeClass("_active");
    $(this).addClass("_active");
})
